

import * as React from 'react';
import "./projects.css"
import { Data } from './const';
import 'animate.css';


const Projects=()=>{
   

console.log(Data)



  
    return(
        <div className="projects-container">
          <div className="headertexts">
            <p className="contact-tittle">My Projects</p>
            <p>Here are some few cool things I've worked on, do check them out!</p>
          </div>
        <div className="projects">
          { Data.map((data)=>{
            return <div class="card2">
            <div class="image2"><img src={data.image} className="card-img"/></div>
             <div class="content2">

                 <span class="title2">
                 {data.tittle}
                 </span>
               <p class="desc2">
               {data.discription}
               </p>
           
               <a href={data.link} class="action2">
                 Check Project
                 <span aria-hidden="true">
                   
                 </span>
               </a>
             </div>
           </div>
          })

          }
        </div>
      </div>
    )
}


export default Projects;